import React, { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";


// Group button variants by collections for better organization
const BUTTON_VARIANTS = {
  // Basic variants
  BASIC: ["primary", "secondary", "tertiary", "basic"],
  // Lebanese-inspired collections
  CEDAR: ["cedar", "cedar-light", "cedar-dark"],
  MEDITERRANEAN: ["sea", "coast", "azure"],
  MOUNTAIN: ["stone", "earth", "terracotta"],
  BEIRUT: ["urban", "concrete", "steel"],
  CULINARY: ["olive", "saffron", "grape"],
  FESTIVAL: ["festive", "celebration", "heritage"],
  CRAFT: ["copper", "silver", "ceramic"],
  MODERN: ["tech", "digital", "startup"],
};

// Define button types
const BUTTON_TYPES = {
  FILLED: "filled", // Default solid background
  OUTLINED: "outlined", // Border only, transparent background
  GHOST: "ghost", // No background, no border, just text color
};

// Define hover effects
const HOVER_EFFECTS = {
  // Basic effects
  DEFAULT: "default",
  NONE: "none",
  // Advanced effects
  THREE_D_ROTATE: "3d-rotate",
  MAGNETIC: "magnetic",
  SHEEN: "sheen",
  NEON: "neon",
  REVEAL: "reveal",
  EXPAND: "expand",
  LIQUID: "liquid",
  SLICE: "slice",
  BOUNCE: "bounce",
  GLITCH: "glitch",
  BORDER_SPIN: "border-spin",
};

// Define icon positions
const ICON_POSITIONS = {
  LEFT: "left",
  RIGHT: "right",
};

// Define tooltip positions
const TOOLTIP_POSITIONS = {
  TOP: "top",
  RIGHT: "right",
  BOTTOM: "bottom",
  LEFT: "left",
};

// Define tooltip themes
const TOOLTIP_THEMES = {
  DEFAULT: "default",
  CEDAR: "cedar",
  SEA: "sea",
  STONE: "stone",
  URBAN: "urban",
  SAFFRON: "saffron",
};

// Define border radius options
const BORDER_RADIUS = {
  NONE: "none", // No border radius (square)
  SMALL: "small", // Slight rounding (4px)
  MEDIUM: "medium", // Default rounding (6px)
  LARGE: "large", // More pronounced rounding (8px)
  PILL: "pill", // Pill shape (9999px)
  ROUNDED: "rounded", // Fully rounded (50%)
};

// Define loading spinner types
const LOADING_SPINNER = {
  DOTS: "dots", // Three dots bouncing
  CIRCLE: "circle", // Circular spinner
  PULSE: "pulse", // Pulsing circle
  BARS: "bars", // Loading bars
  ELLIPSIS: "ellipsis", // Text ellipsis (...)
};

// Flatten variants for PropTypes validation
const ALL_VARIANTS = Object.values(BUTTON_VARIANTS).flat();
const ALL_BUTTON_TYPES = Object.values(BUTTON_TYPES);
const ALL_HOVER_EFFECTS = Object.values(HOVER_EFFECTS);
const ALL_ICON_POSITIONS = Object.values(ICON_POSITIONS);
const ALL_TOOLTIP_POSITIONS = Object.values(TOOLTIP_POSITIONS);
const ALL_TOOLTIP_THEMES = Object.values(TOOLTIP_THEMES);
const ALL_BORDER_RADIUS = Object.values(BORDER_RADIUS);
const ALL_LOADING_SPINNER = Object.values(LOADING_SPINNER);

// Helper to get collection name from variant
const getCollectionFromVariant = (variant) => {
  return (
    Object.entries(BUTTON_VARIANTS)
      .find(([_, variants]) => variants.includes(variant))?.[0]
      ?.toLowerCase() || "basic"
  );
};

// Main Button component that supports all variants
const Button = ({
  children,
  variant = "primary",
  buttonType = BUTTON_TYPES.FILLED,
  size = "medium",
  disabled = false,
  onClick,
  className = "",
  style = {},
  hoverEffect = HOVER_EFFECTS.DEFAULT,
  icon = null,
  iconPosition = ICON_POSITIONS.LEFT,
  iconOnly = false,
  iconSpacing = "0.5rem",
  iconClassName = "",
  iconStyle = {},
  tooltip = null,
  tooltipPosition = TOOLTIP_POSITIONS.TOP,
  tooltipDelay = 300,
  tooltipClassName = "",
  tooltipStyle = {},
  tooltipTheme = TOOLTIP_THEMES.DEFAULT,
  borderRadius = BORDER_RADIUS.MEDIUM,
  loading = false,
  loadingText = null,
  loadingSpinner = LOADING_SPINNER.CIRCLE,
  loadingPosition = ICON_POSITIONS.LEFT,
  hideTextWhenLoading = false,
  ...props
}) => {
  // State for tooltip visibility
  const [showTooltip, setShowTooltip] = useState(false);
  const [tooltipVisible, setTooltipVisible] = useState(false);
  const [tooltipCoords, setTooltipCoords] = useState({ top: 0, left: 0 });
  const [tooltipReady, setTooltipReady] = useState(false);
  const buttonRef = useRef(null);
  const tooltipRef = useRef(null);
  const tooltipTimeout = useRef(null);
  const tooltipExitTimeout = useRef(null);
  const tooltipReadyTimeout = useRef(null);

  // Determine if we have content besides the icon
  const hasContent = React.Children.count(children) > 0;

  // Determine if we should show text during loading
  const showTextWhenLoading =
    !hideTextWhenLoading && (loadingText !== null || hasContent);

  // Build class names
  const buttonClass = `
    lebify-button 
    lebify-button-${variant} 
    lebify-button-${size}
    lebify-button-${buttonType}
    lebify-button-radius-${borderRadius}
    ${disabled ? "lebify-button-disabled" : ""}
    ${loading ? "lebify-button-loading" : ""}
    ${loading && !showTextWhenLoading ? "lebify-button-loading-no-text" : ""}
    ${
      hoverEffect !== HOVER_EFFECTS.DEFAULT ? `lebify-hover-${hoverEffect}` : ""
    }
    ${icon ? `lebify-button-icon-${iconPosition}` : ""}
    ${iconOnly ? "lebify-button-icon-only" : ""}
    ${icon && !iconOnly ? "lebify-button-with-icon" : ""}
    ${className}
  `
    .trim()
    .replace(/\s+/g, " ");

  const iconClass = `lebify-button-icon ${iconClassName}`.trim();

  const tooltipClass = `
    lebify-tooltip
    lebify-tooltip-${tooltipPosition}
    ${
      tooltipTheme !== TOOLTIP_THEMES.DEFAULT
        ? `lebify-tooltip-${tooltipTheme}`
        : ""
    }
    ${
      tooltipVisible && tooltipReady
        ? "lebify-tooltip-visible"
        : "lebify-tooltip-hidden"
    }
    ${tooltipClassName}
  `
    .trim()
    .replace(/\s+/g, " ");

  const loadingSpinnerClass = `lebify-spinner lebify-spinner-${loadingSpinner} lebify-spinner-${size}`;

  const iconSpacingStyle = {
    ...(iconPosition === ICON_POSITIONS.LEFT
      ? { marginRight: iconOnly ? 0 : iconSpacing }
      : {}),
    ...(iconPosition === ICON_POSITIONS.RIGHT
      ? { marginLeft: iconOnly ? 0 : iconSpacing }
      : {}),
    ...iconStyle,
  };

  const loadingSpacingStyle = {
    ...(loadingPosition === ICON_POSITIONS.LEFT && showTextWhenLoading
      ? { marginRight: iconSpacing }
      : {}),
    ...(loadingPosition === ICON_POSITIONS.RIGHT && showTextWhenLoading
      ? { marginLeft: iconSpacing }
      : {}),
  };

  // Calculate tooltip position when it becomes visible
  useEffect(() => {
    if (showTooltip && buttonRef.current && tooltipRef.current) {
      const buttonRect = buttonRef.current.getBoundingClientRect();
      const tooltipRect = tooltipRef.current.getBoundingClientRect();

      let top = 0;
      let left = 0;

      // Calculate position based on tooltip position
      switch (tooltipPosition) {
        case TOOLTIP_POSITIONS.TOP:
          top = buttonRect.top - tooltipRect.height - 8;
          left = buttonRect.left + buttonRect.width / 2 - tooltipRect.width / 2;
          break;
        case TOOLTIP_POSITIONS.RIGHT:
          top = buttonRect.top + buttonRect.height / 2 - tooltipRect.height / 2;
          left = buttonRect.right + 8;
          break;
        case TOOLTIP_POSITIONS.BOTTOM:
          top = buttonRect.bottom + 8;
          left = buttonRect.left + buttonRect.width / 2 - tooltipRect.width / 2;
          break;
        case TOOLTIP_POSITIONS.LEFT:
          top = buttonRect.top + buttonRect.height / 2 - tooltipRect.height / 2;
          left = buttonRect.left - tooltipRect.width - 8;
          break;
        default:
          break;
      }

      // Adjust for scroll position
      top += window.scrollY;
      left += window.scrollX;

      setTooltipCoords({ top, left });

      // Set tooltip as ready after position is calculated
      // This ensures the tooltip is properly positioned before becoming visible
      if (!tooltipReady) {
        tooltipReadyTimeout.current = setTimeout(() => {
          setTooltipReady(true);
        }, 20); // Small delay to ensure DOM updates
      }
    }
  }, [showTooltip, tooltipPosition, tooltip, tooltipReady]);

  // Handle mouse events for tooltip
  const handleMouseEnter = () => {
    if (tooltip && !disabled) {
      // Clear any existing timeouts
      if (tooltipExitTimeout.current) {
        clearTimeout(tooltipExitTimeout.current);
        tooltipExitTimeout.current = null;
      }

      if (tooltipReadyTimeout.current) {
        clearTimeout(tooltipReadyTimeout.current);
        tooltipReadyTimeout.current = null;
      }

      tooltipTimeout.current = setTimeout(() => {
        setTooltipReady(false); // Reset ready state
        setShowTooltip(true);

        // Small delay to ensure DOM is updated before animation starts
        requestAnimationFrame(() => {
          requestAnimationFrame(() => {
            setTooltipVisible(true);
          });
        });
      }, tooltipDelay);
    }
  };

  const handleMouseLeave = () => {
    if (tooltipTimeout.current) {
      clearTimeout(tooltipTimeout.current);
      tooltipTimeout.current = null;
    }

    if (tooltipReadyTimeout.current) {
      clearTimeout(tooltipReadyTimeout.current);
      tooltipReadyTimeout.current = null;
    }

    if (showTooltip) {
      setTooltipVisible(false);

      // Keep the tooltip in DOM during exit animation
      tooltipExitTimeout.current = setTimeout(() => {
        setShowTooltip(false);
        setTooltipReady(false);
      }, 200); // Match this with CSS transition duration
    }
  };

  // Clean up timeouts on unmount
  useEffect(() => {
    return () => {
      if (tooltipTimeout.current) {
        clearTimeout(tooltipTimeout.current);
      }
      if (tooltipExitTimeout.current) {
        clearTimeout(tooltipExitTimeout.current);
      }
      if (tooltipReadyTimeout.current) {
        clearTimeout(tooltipReadyTimeout.current);
      }
    };
  }, []);

  // Render loading spinner based on type
  const renderLoadingSpinner = () => {
    switch (loadingSpinner) {
      case LOADING_SPINNER.DOTS:
        return (
          <div className={loadingSpinnerClass}>
            <div className="dot"></div>
            <div className="dot"></div>
            <div className="dot"></div>
          </div>
        );
      case LOADING_SPINNER.CIRCLE:
        return (
          <div className={loadingSpinnerClass}>
            <svg viewBox="0 0 50 50">
              <circle
                cx="25"
                cy="25"
                r="20"
                fill="none"
                strokeWidth="5"
              ></circle>
            </svg>
          </div>
        );
      case LOADING_SPINNER.PULSE:
        return (
          <div className={loadingSpinnerClass}>
            <div className="pulse"></div>
          </div>
        );
      case LOADING_SPINNER.BARS:
        return (
          <div className={loadingSpinnerClass}>
            <div className="bar"></div>
            <div className="bar"></div>
            <div className="bar"></div>
            <div className="bar"></div>
          </div>
        );
      case LOADING_SPINNER.ELLIPSIS:
        return (
          <div className={loadingSpinnerClass}>
            <div className="dot"></div>
            <div className="dot"></div>
            <div className="dot"></div>
          </div>
        );
      default:
        return (
          <div className={loadingSpinnerClass}>
            <svg viewBox="0 0 50 50">
              <circle
                cx="25"
                cy="25"
                r="20"
                fill="none"
                strokeWidth="5"
              ></circle>
            </svg>
          </div>
        );
    }
  };

  // Render the button with icon and tooltip
  return (
    <>
      <button
        ref={buttonRef}
        className={buttonClass}
        onClick={disabled || loading ? undefined : onClick}
        disabled={disabled}
        style={style}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onFocus={handleMouseEnter}
        onBlur={handleMouseLeave}
        {...(iconOnly &&
          !loading && {
            "aria-label":
              props["aria-label"] ||
              (typeof children === "string" ? children : undefined),
          })}
        {...props}
      >
        <div className="lebify-button-content">
          {/* Left-positioned loading spinner */}
          {loading && loadingPosition === ICON_POSITIONS.LEFT && (
            <span
              className="lebify-button-loading-spinner"
              style={loadingSpacingStyle}
            >
              {renderLoadingSpinner()}
            </span>
          )}

          {/* Left-positioned icon (when not loading) */}
          {!loading &&
            icon &&
            (iconPosition === ICON_POSITIONS.LEFT || iconOnly) && (
              <span className={iconClass} style={iconSpacingStyle}>
                {icon}
              </span>
            )}

          {/* Button text content */}
          {(!loading || showTextWhenLoading) && (
            <span className="lebify-button-text">
              {loading && loadingText
                ? loadingText
                : !iconOnly && hasContent && children}
            </span>
          )}

          {/* Right-positioned icon (when not loading) */}
          {!loading &&
            icon &&
            iconPosition === ICON_POSITIONS.RIGHT &&
            !iconOnly && (
              <span className={iconClass} style={iconSpacingStyle}>
                {icon}
              </span>
            )}

          {/* Right-positioned loading spinner */}
          {loading && loadingPosition === ICON_POSITIONS.RIGHT && (
            <span
              className="lebify-button-loading-spinner"
              style={loadingSpacingStyle}
            >
              {renderLoadingSpinner()}
            </span>
          )}
        </div>
      </button>

      {tooltip && showTooltip && (
        <div
          ref={tooltipRef}
          className={tooltipClass}
          style={{
            ...tooltipStyle,
            position: "absolute",
            top: `${tooltipCoords.top}px`,
            left: `${tooltipCoords.left}px`,
          }}
          role="tooltip"
        >
          {tooltip}
        </div>
      )}
    </>
  );
};

Button.propTypes = {
  children: PropTypes.node,
  variant: PropTypes.oneOf(ALL_VARIANTS),
  buttonType: PropTypes.oneOf(ALL_BUTTON_TYPES),
  size: PropTypes.oneOf(["small", "medium", "large"]),
  disabled: PropTypes.bool,
  onClick: PropTypes.func,
  className: PropTypes.string,
  style: PropTypes.object,
  hoverEffect: PropTypes.oneOf(ALL_HOVER_EFFECTS),
  icon: PropTypes.node,
  iconPosition: PropTypes.oneOf(ALL_ICON_POSITIONS),
  iconOnly: PropTypes.bool,
  iconSpacing: PropTypes.string,
  iconClassName: PropTypes.string,
  iconStyle: PropTypes.object,
  tooltip: PropTypes.node,
  tooltipPosition: PropTypes.oneOf(ALL_TOOLTIP_POSITIONS),
  tooltipDelay: PropTypes.number,
  tooltipClassName: PropTypes.string,
  tooltipStyle: PropTypes.object,
  tooltipTheme: PropTypes.oneOf(ALL_TOOLTIP_THEMES),
  borderRadius: PropTypes.oneOf(ALL_BORDER_RADIUS),
  loading: PropTypes.bool,
  loadingText: PropTypes.string,
  loadingSpinner: PropTypes.oneOf(ALL_LOADING_SPINNER),
  loadingPosition: PropTypes.oneOf(ALL_ICON_POSITIONS),
  hideTextWhenLoading: PropTypes.bool,
};

// Export the variants and hover effects for external use
Button.VARIANTS = BUTTON_VARIANTS;
Button.TYPES = BUTTON_TYPES;
Button.HOVER_EFFECTS = HOVER_EFFECTS;
Button.ICON_POSITIONS = ICON_POSITIONS;
Button.TOOLTIP_POSITIONS = TOOLTIP_POSITIONS;
Button.TOOLTIP_THEMES = TOOLTIP_THEMES;
Button.BORDER_RADIUS = BORDER_RADIUS;
Button.LOADING_SPINNER = LOADING_SPINNER;

export default Button;
